.CurrencySelect {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.CurrencySelect__icon {
  margin-right: 4px;
}

.CurrencySelect__text {
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
}

.CurrencySelect__arrow {
  margin-left: 0px;
}

.CurrencySelect--disabled {
  cursor: default;
  .CurrencySelect__arrow {
    display: none;
  }
}
