@import "styles/colors-functions";

.ProceedButton {
  position: relative;
  padding: 10px;
  height: 76px;
  font-size: 17px;
  font-weight: 700;
  display: inline-flex;
  align-items: center;
  text-align: center;
  color: useColor(--colorWhite);
  background-color: useColor(--colorDarkBlue);
  border-radius: 12px;
  padding: 0px 24px;
  display: grid;
  grid-template-columns: 1fr auto 1fr;

  transition: background-color 0.1s ease;
}

.ProceedButton--active:hover {
  background-color: useColor(--colorAccent2);
}

.ProceedButton--disabled {
  background-color: useColor(--colorDisabled1);
  color: useColor(--colorDisabled2);
  cursor: not-allowed;
}
.ProceedButton--loading {
  cursor: default;
  color: useColor(--colorDisabled2);
  background-color: useColor(--colorDisabled1);
}
.ProceedButton__arrow {
  margin-left: auto;
}

.ProceedButton__icon {
  position: absolute;
  right: 24px;
}
