.pay-with-near-pay-widget .ProceedButton {
  width: 100%;
  margin-bottom: 28px;
}

.pay-with-near-pay-widget {
  &__title {
    text-align: center;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 28px;
  }

  &__description {
    max-width: 400px;
    font-size: 14px;
    text-align: center;
    margin-bottom: 28px;
  }

  &__or {
    font-weight: 700;
    font-size: 18px;
    color: #6b767d;
    margin-bottom: 28px;
  }

  &__qr-code-container {
    background: white;
    padding: 13px;
    border-radius: 17px;
    margin-bottom: 20px;
  }

  &__payment-method-text {
    text-align: center;
    font-size: 13px;
    font-weight: 400;
    color: #6b767d;
    margin-bottom: 40px;
  }

  &__link-nearpay {
    text-decoration: none;
    font-size: 13px;
    font-weight: 600;
    color: #000000;
    &_gray {
      color: #6b767d;
      font-weight: 400;
    }
    &_icon {
      display: inline;
      vertical-align: middle;
    }
  }
}

.pay-with-near-pay-widget-description {
  font-size: 13px;
  width: 100%;
  margin-bottom: 28px;

  & > div:first-child {
    margin-bottom: 20px;
  }

  &__label {
    font-weight: 600;
    margin-bottom: 8px;
  }
}
