@keyframes errorAppear {
  from {
    opacity: 0;
    transform: translate(0px, 5px);
  }
  to {
    opacity: 1;
    transform: translate(0px, 0px);
  }
}

.Input {
  padding: 20px;
  background-color: white;
  border-radius: 17px;
  color: rgb(8, 27, 39);
  cursor: text;
  position: relative;
  display: block;
}

.Input input,
.Input__input {
  background-color: white;

  outline: none;
  line-height: 1;
  min-width: 0;
  width: 100%;
}

.Input input::placeholder,
.Input__inputPlaceholder {
  color: rgb(181, 187, 190);
}
.Input input[disabled],
.Input__input--disabled {
  color: rgb(107, 118, 125);
}

.Input__error {
  color: rgb(224, 51, 93);
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
  animation: errorAppear 0.2s ease;
}
.Input__header {
  height: 17px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Input__label {
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 16px;
}

.Input__wrapper {
  display: flex;
  flex-grow: 1;
}

.Input__after {
  align-self: flex-end;
  flex-shrink: 0;
  margin-left: 12px;
}

.Input__afterCentered {
  display: flex;
  align-items: center;
  margin-left: 12px;
}

.Input--hasError {
  padding-bottom: 8px;
  box-shadow: inset 0px 0px 0px 2px rgb(224, 51, 93);
}
.Input:focus-within {
  box-shadow: inset 0px 0px 0px 2px rgb(8, 27, 39);
}

input[type="text"] {
  border: none;
  color: #081b27;
  font-size: 28px;
  font-weight: 600;
}
